import css from './ProfileInfo.module.css'
import Preloader from "../../common/Preloader/Preloader";
import avatar from "../../../assets/images/avatar.jpg"
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props) => {
    if (!props.profile){
        return <Preloader />
    }
    return (
        <div >
            {/*<div>*/}
            {/*    <img className={css.content__img} alt="background"*/}
            {/*          src='https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg'/>*/}
            {/*</div>*/}
            <div className={css.description}>
                <img className={css.content__avatar} alt="avatar" src={props.profile.photos.small ? props.profile.photos.small : avatar}/>
                Ищу ли я работу? - {props.profile.lookingForAJobDescription}
                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
            </div>
            </div>
    )
}
export default ProfileInfo;