import css from './MyPosts.module.css'
import Text from './Post/Post1'
import React from "react";
import MyPostsForm from "./MyPostsForm";

const MyPosts = (props) => {

    let postsElements = props.posts.map( post => <Text message={post.message} key={post.id} likecount={post.likes}/> )

    return (
        <div className={css.item}>

            <MyPostsForm addPost={props.addPost}/>

            <div className={css.posts}>New post</div>
            {postsElements}
        </div>
    )
}

export default MyPosts;